import { StyleRule, StyleClass } from "../utils/DOM";
import { InterfaceColorSet } from "../utils/InterfaceColorSet";
import { Dictionary } from "../utils/Dictionary";
import { MultiDisposer, IDisposer, CounterDisposer } from "../utils/Disposer";

const rules = new Dictionary<string, CounterDisposer>();

/**
 * A dynamically-loadable CSS module for Export menu.
 *
 * @ignore Exclude from docs
 * @param  {string}     prefix  Prefix to addtach to class names
 * @return {IDisposer}          A MultiDisposer with style rules
 */
export default function (prefix?: string): IDisposer {
	if (!prefix) {
		prefix = "amexport";
	}

	let colorSet = new InterfaceColorSet();
	let fill = colorSet.getFor("fill");
	let border = colorSet.getFor("stroke");

	const counter = rules.insertKeyIfEmpty(prefix, () => {
		const disposer = new MultiDisposer([

			/*new StyleRule(`.${prefix}-menu`, {
				"opacity": "0.3",
				"transition": "all 100ms ease-in-out",
			}),

			new StyleRule(`div:hover .${prefix}-menu, .${prefix}-menu.active`, {
				"opacity": "0.9",
			}),*/

			new StyleRule(`.${prefix}-menu-level-0`, {
				"position": "absolute",
				"top": "5px",
				"right": "5px",
			}),

			new StyleRule(`.${prefix}-menu-level-0.${prefix}-left`, {
				"right": "auto",
				"left": "5px",
			}),

			new StyleRule(`.${prefix}-menu-level-0.${prefix}-right`, {
				"right": "5px",
				"left": "auto",
			}),

			new StyleRule(`.${prefix}-menu-level-0.${prefix}-top`, {
				"top": "5px",
				"bottom": "auto",
			}),

			new StyleRule(`.${prefix}-menu-level-0.${prefix}-bottom`, {
				"top": "auto",
				"bottom": "5px",
			}),

			new StyleRule(`.${prefix}-item.${prefix}-item-level-0`, {
				"opacity": "0.3",
				"width": "30px",
				"height": "30px",
				"transition": "all 100ms ease-in-out",
				//"text-indent": "-9999px",
			}),

			new StyleRule(`div:hover .${prefix}-item.${prefix}-item-level-0, .${prefix}-item.${prefix}-item-level-0.active`, {
				"opacity": "0.9",
			}),

			new StyleRule(`.${prefix}-item.${prefix}-item-level-0 > a`, {
				"padding": "0",
				"text-align": "center"
			}),

			new StyleRule(`.${prefix}-item.${prefix}-item-level-0:before`, {
				"display": "block",
				"content": "..."
			}),

			new StyleRule(`.${prefix}-item`, {
				"position": "relative",
				"display": "block",
				"opacity": "0",
				"z-index": "1",
				"border-radius": "3px",
				"background-color": colorSet.getFor("secondaryButton").hex,
				"padding": "0",
				"margin": "1px 1px 0 0",
				"color": colorSet.getFor("secondaryButton").alternative.hex,
				"transition": "all 100ms ease-in-out, opacity 0.5s ease 0.5s",
				"width": "initial",
				"height": "initial",
				"text-indent": "initial",
			}),

			new StyleRule(`.${prefix}-left .${prefix}-item`, {
				"margin": "1px 0 0 1px",
			}),

			new StyleRule(`.${prefix}-item:hover, .${prefix}-item.active`, {
				"background": colorSet.getFor("secondaryButtonHover").hex,
				"color": colorSet.getFor("secondaryButtonText").hex,
			}),

			new StyleRule(`.${prefix}-item > .${prefix}-menu`, {
				"position": "absolute",
				"top": "-1px",
				"right": "0",
				"margin-right": "100%",
			}),

			new StyleRule(`.${prefix}-left .${prefix}-item > .${prefix}-menu`, {
				"left": "0",
				"right": "auto",
				"margin-left": "100%",
				"margin-right": "auto",
			}),

			new StyleRule(`.${prefix}-right .${prefix}-item > .${prefix}-menu`, {
				"left": "auto",
				"right": "0",
				"margin-left": "auto",
				"margin-right": "100%",
			}),

			new StyleRule(`.${prefix}-top .${prefix}-item > .${prefix}-menu`, {
				"top": "-1px",
				"bottom": "auto",
			}),

			new StyleRule(`.${prefix}-bottom .${prefix}-item > .${prefix}-menu`, {
				"top": "auto",
				"bottom": "0",
			}),

			new StyleRule(`.${prefix}-item > .${prefix}-menu`, {
				"display": "none",
			}),

			new StyleRule(`.${prefix}-item:hover > .${prefix}-menu, .${prefix}-item.active > .${prefix}-menu`, {
				"display": "block",
			}),

			new StyleRule(`.${prefix}-item:hover > .${prefix}-menu > .${prefix}-item, .${prefix}-item.active > .${prefix}-menu > .${prefix}-item`, {
				"opacity": "1",
			}),

			new StyleRule(`.${prefix}-menu`, {
				"display": "block",
				"list-style": "none",
				"margin": "0",
				"padding": "0",
			}),

			new StyleRule(`.${prefix}-label`, {
				"display": "block",
				"cursor": "default",
				"padding": "0.5em 1em",
			}),

			new StyleRule(`.${prefix}-clickable`, {
				"cursor": "pointer",
			}),
		]);

		return new CounterDisposer(() => {
			rules.removeKey(prefix);
			disposer.dispose();
		});
	});

	return counter.increment();
}


